
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'CheckboxBlock',
  props: {
    id: String,
    label: String,
    checkboxValue: Boolean,
  },
  setup(props, { emit }) {
    const toggleCheckboxValue = () => {
      emit(`update-${props.id}`, !props.checkboxValue);
    };

    return {
      toggleCheckboxValue,
    };
  },
});
